/* src/styles/SelectionScreen.css */
@import url('https://fonts.googleapis.com/css2?family=Jua&display=swap');

.selection-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  background-color: #f0f8ff;
  font-family: 'Jua', sans-serif;
}

.selection-header {
  width: 100%;
  max-width: 800px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
  margin-bottom: 20px;
  border-bottom: 2px solid #ddd;
}

.user-name {
  font-size: 1.5rem;
  color: #333;
}

.logout-button {
  width: 35px;
  height: 35px;
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 1.8rem;
  line-height: 35px;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s;
}

.logout-button:hover {
  background-color: #c0392b;
  transform: rotate(90deg);
}

.selection-container {
  width: 100%;
  max-width: 800px;
  background-color: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.selection-title {
  text-align: center;
  font-size: 2.2rem;
  color: #2980b9;
  margin-bottom: 10px;
}

.menu-section h3 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #555;
  border-left: 4px solid #3498db;
  padding-left: 10px;
}

.options-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 10px;
}

.options-grid-dynamic {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 10px;
}

.option-button {
  padding: 12px;
  font-size: 1rem;
  font-family: 'Jua', sans-serif;
  border: 2px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  cursor: pointer;
  transition: all 0.2s;
}

.option-button:hover {
  border-color: #3498db;
  color: #3498db;
}

.option-button.active {
  background-color: #3498db;
  color: white;
  border-color: #3498db;
  font-weight: bold;
}

.start-quiz-button {
  width: 100%;
  padding: 18px;
  font-size: 1.5rem;
  font-family: 'Jua', sans-serif;
  color: white;
  background-color: #2ecc71;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 20px;
}

.start-quiz-button:hover:not(:disabled) {
  background-color: #27ae60;
  transform: translateY(-2px);
}

.start-quiz-button:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.history-button, .sound-button {
  padding: 10px 20px;
  font-size: 1rem;
  font-family: 'Jua', sans-serif;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}

.history-button {
  background-color: #9b59b6;
  color: white;
}
.history-button:hover {
  background-color: #8e44ad;
}

.sound-button.on {
  background-color: #f39c12;
  color: white;
}
.sound-button.off {
  background-color: #7f8c8d;
  color: white;
}